export default {
  name: 'unlocking-screen',
  data () {
    return {
      countDown: 2
    }
  },
  mounted () {
    const timer = setInterval(() => {
      this.countDown = this.countDown - 1
      if (this.countDown <= 0) {
        clearInterval(timer)
        this.$router.push({name: 'UnlockedCodeScreen'})
      }
    }, 1000)
  }
}



